import { Router } from "express";
import { multerConfig } from "../config/multer";
//import multer from "multer";
import {
  invoiceShowRange,
  invoiceShow,
  invoiceDelete,
  invoiceStore
} from "../controllers/invoiceController";
import { clear } from "../functions/clear";


// Create a new router
const router: Router = Router();
router.post("/invoice/store", invoiceStore);

router.get("/invoice/show", invoiceShow);

router.get("/invoice/showRange",invoiceShowRange);

router.delete("/invoice/delete", invoiceDelete);


router.delete("/clear", (req, res) => {
  res.json(clear());
});

